import React from 'react';
import 'weather-icons/css/weather-icons.css';

function Input(props) {

    let records0, records1, records2, records3, records4, country0, country1, country2, country3, country4 = null;

    const searchSelection = (name, alternate_names, country_code, numOfEl) => {
        let split_names = [];
        if (alternate_names) split_names = alternate_names.split(",");
        split_names.push(name);

        let res = [];
        for (let index = 0; index < split_names.length; index++) {
            const element = split_names[index];
            if (element.startsWith(props.inputValue) === true || element.includes(props.inputValue)) {
                res.push(element);
            }
        }

        console.log('country_code >>>', country_code);

        if (numOfEl === 0) {
            records0 = res[0];
            country0 = country0.toLowerCase();
        }
        else if (numOfEl === 1)  {
            records1 = res[0];
            country1 = country1.toLowerCase();
        }
        else if (numOfEl === 2)  {
            records2 = res[0];
            country2 = country2.toLowerCase();
        }
        else if (numOfEl === 3)  {
            records3 = res[0];
            country3 = country3.toLowerCase();
        }
        else if (numOfEl === 4)  {
            records4 = res[0];
            country4 = country4.toLowerCase();
        }

         console.log('records0 >>>', records0);
         console.log('records1 >>>', records1);
         console.log('records2 >>>', records2);
         console.log('records3 >>>', records3);
         console.log('records4 >>>', records4);
         console.log('country0 >>>', country0);
         console.log('country1 >>>', country1);
         console.log('country2 >>>', country2);
         console.log('country3 >>>', country3);
         console.log('country4 >>>', country4);
    }

    let name0, alternate_names0, name1, alternate_names1, name2, alternate_names2, name3, alternate_names3, name4, alternate_names4 = null;
    switch (props.finalArr.length) {
        case 1:
            name0 = props.finalArr[0].fields.name;
            alternate_names0 = props.finalArr[0].fields.alternate_names;
            country0 = props.finalArr[0].fields.country_code;
            searchSelection(name0, alternate_names0, country0, 0);
            records1 = null; records2 = null; records3 = null; records4 = null;
            break;
        case 2:
            name0 = props.finalArr[0].fields.name; alternate_names0 = props.finalArr[0].fields.alternate_names; country0 = props.finalArr[0].fields.country_code;
            searchSelection(name0, alternate_names0, country0, 0);
            name1 = props.finalArr[1].fields.name; alternate_names1 = props.finalArr[1].fields.alternate_names;
            country1 = props.finalArr[1].fields.country_code;
            searchSelection(name1, alternate_names1, country1, 1);
            records2 = null; records3 = null; records4 = null;
            break;
        case 3:
            name0 = props.finalArr[0].fields.name; alternate_names0 = props.finalArr[0].fields.alternate_names; country0 = props.finalArr[0].fields.country_code;
            searchSelection(name0, alternate_names0, country0, 0);
            name1 = props.finalArr[1].fields.name; alternate_names1 = props.finalArr[1].fields.alternate_names; country1 = props.finalArr[1].fields.country_code;
            searchSelection(name1, alternate_names1, country1, 1);
            name2 = props.finalArr[2].fields.name; alternate_names2 = props.finalArr[2].fields.alternate_names; country2 = props.finalArr[2].fields.country_code;
            searchSelection(name2, alternate_names2, country2, 2);
            records3 = null;  records4 = null;
            break;
        case 4:
            name0 = props.finalArr[0].fields.name; alternate_names0 = props.finalArr[0].fields.alternate_names; country0 = props.finalArr[0].fields.country_code;
            searchSelection(name0, alternate_names0, country0, 0);
            name1 = props.finalArr[1].fields.name; alternate_names1 = props.finalArr[1].fields.alternate_names; country1 = props.finalArr[1].fields.country_code;
            searchSelection(name1, alternate_names1, country1, 1);
            name2 = props.finalArr[2].fields.name; alternate_names2 = props.finalArr[2].fields.alternate_names; country2 = props.finalArr[2].fields.country_code;
            searchSelection(name2, alternate_names2, country2, 2);
            name3 = props.finalArr[3].fields.name; alternate_names3 = props.finalArr[3].fields.alternate_names; country3 = props.finalArr[3].fields.country_code;
            searchSelection(name3, alternate_names3, country3, 3);
            records4 = null;
            break;
        case 5:
            name0 = props.finalArr[0].fields.name; alternate_names0 = props.finalArr[0].fields.alternate_names; country0 = props.finalArr[0].fields.country_code;
            searchSelection(name0, alternate_names0, country0, 0);
            name1 = props.finalArr[1].fields.name; alternate_names1 = props.finalArr[1].fields.alternate_names; country1 = props.finalArr[1].fields.country_code;
            searchSelection(name1, alternate_names1, country1, 1);
            name2 = props.finalArr[2].fields.name; alternate_names2 = props.finalArr[2].fields.alternate_names; country2 = props.finalArr[2].fields.country_code;
            searchSelection(name2, alternate_names2, country2, 2);
            name3 = props.finalArr[3].fields.name; alternate_names3 = props.finalArr[3].fields.alternate_names; country3 = props.finalArr[3].fields.country_code;
            searchSelection(name3, alternate_names3, country3, 3);
            name4 = props.finalArr[4].fields.name; alternate_names4 = props.finalArr[4].fields.alternate_names; country4 = props.finalArr[4].fields.country_code;
            searchSelection(name4, alternate_names4, country4, 4);
            break;
    
        default:
            records0 = null; records1 = null; records2 = null; records3 = null; records4 = null;
            break;
    }
    let disableSearch = () => {
        setTimeout(() => {
            document.getElementById('searchTable').style.display = 'none';
        }, 300);
        // document.getElementById('searchTable').style.display = 'none';
    }
    return (
        <div className="searchBox">
            <form onSubmit={props.loadWeather} id=".move">
                <button id="inputBtn">Посмотреть</button>
                <input name="searchBar"
                className="searchBar"
                id="searchBar"
                placeholder="Введите ваш город"
                onInput={props.searchLive}
                autoComplete="off"
                onBlur={disableSearch}
                maxLength="20"
                onFocus={() => {document.getElementById('searchBar').value='';}}
                ></input>
                
            </form>
            <table id="searchTable">
                <tbody>
                    <tr>
                        <th>
                            <button
                            id="btn0"
                            onClick={(event)=>props.clearInput(name0, name1, name2, name3, name4, event, country0, country1, country2, country3, country4)}>
                                {records0}
                                <img className="searchBtn" src={`./flags/${country0}.png`} alt="flag" />
                            </button>
                        </th>
                    </tr>
                    <tr>
                        <th>
                            <button
                            id="btn1"
                            onClick={(event)=>props.clearInput(name0, name1, name2, name3, name4, event, country0, country1, country2, country3, country4)}>
                                {records1}
                                <img className="searchBtn" src={`./flags/${country1}.png`} alt="flag" />
                            </button>
                        </th>
                    </tr>
                    <tr>
                        <th>
                            <button
                            id="btn2"
                            onClick={(event)=>props.clearInput(name0, name1, name2, name3, name4, event, country0, country1, country2, country3, country4)}>
                                {records2}
                                <img className="searchBtn" src={`./flags/${country2}.png`} alt="flag" />
                            </button>
                        </th>
                    </tr>
                    <tr>
                        <th>
                            <button
                            id="btn3"
                            onClick={(event)=>props.clearInput(name0, name1, name2, name3, name4, event, country0, country1, country2, country3, country4)}>
                                {records3}
                                <img className="searchBtn" src={`./flags/${country3}.png`} alt="flag" />
                            </button>
                        </th>
                    </tr>
                    <tr>
                        <th>
                            <button
                            id="btn4"
                            onClick={(event)=>props.clearInput(name0, name1, name2, name3, name4, event, country0, country1, country2, country3, country4)}>
                                {records4}
                                <img className="searchBtn" src={`./flags/${country4}.png`} alt="flag" />
                            </button>
                        </th>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}
    
export default Input;